body {
  background-color: #f5f5f5;
  margin: 0;
  font-family: Arial, sans-serif;
}

.header-top {
  background-color: #eaba22;
  padding: 10px 20px;
}

.header-content {
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.header-logo {
  border: 2px solid #333;
  border-radius: 50%;
  width: 60px;
  height: auto;
  margin-right: 15px;
}

.header-title {
  color: #333;
  margin: 0;
  font-size: 24px;
}

.searchbox {
  background-color: #fff;
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px;
  box-shadow: 0 2px 4px #0000001a;
}

.container {
  max-width: 1200px;
  height: calc(100vh - 140px);
  margin: 0 auto;
  display: flex;
  overflow: hidden;
}

.sidebar {
  box-sizing: border-box;
  background-color: #fff;
  flex-shrink: 0;
  width: 20%;
  padding: 20px;
  box-shadow: 2px 0 5px #0000001a;
}

.main-content {
  box-sizing: border-box;
  background-color: #fff;
  flex-direction: column;
  flex: 1;
  padding: 10px;
  display: flex;
  overflow-y: auto;
}

#hits {
  flex-grow: 1;
  width: 100%;
  padding: 0 10px;
  overflow-y: auto;
}

.ais-Hits-list {
  flex-wrap: wrap;
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.ais-Hits-item {
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  flex: 0 0 calc(20% - 16px);
  width: calc(20% - 16px);
  max-width: calc(20% - 16px);
  margin-bottom: 20px;
  transition: transform .2s ease-in-out;
  box-shadow: 0 2px 10px #0000001a;
}

.ais-Hits-item:hover {
  transform: translateY(-5px);
}

.hit {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 8px;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 15px;
  display: flex;
}

.hit-image {
  object-fit: cover;
  border-radius: 4px;
  width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.hit-content {
  flex-direction: column;
  flex-grow: 1;
  justify-content: space-between;
  font-size: .9em;
  display: flex;
}

#pagination {
  justify-content: center;
  margin-top: 20px;
  display: flex;
}

.ais-Pagination-list {
  padding: 0;
  list-style-type: none;
  display: flex;
}

.ais-Pagination-item {
  margin: 0 5px;
}

.ais-Pagination-link {
  color: #333;
  background-color: #f0f0f0;
  border-radius: 4px;
  padding: 5px 10px;
  text-decoration: none;
}

.ais-Pagination-item--selected .ais-Pagination-link {
  color: #fff;
  background-color: #eaba22;
}
